/* 表格状态前圆形使用样式 */
.circle-status span {
  vertical-align: middle;
  display: inline-block;
}
.circle-status span:nth-child(1) {
  border-radius: 50%;
  width: 24px;
  height: 24px;
  margin-right: 5px;
}
.circle-status .circle-green {
  background: url("../../assets/alreadySent.png");
}
.circle-status .circle-deepGreen {
  background: url("../../assets/responded.png");
}
.circle-status .circle-blue {
  background: url("../../assets/completed.png");
}
.circle-status .circle-red {
  background: url("../../assets/undone.png");
}
.circle-status .circle-gray {
  background: url("../../assets/alreadySent.png");
}
.circle-status .circle-yellow {
  background: url("../../assets/noOrder.png");
}
.circle-status .circle-reject {
  background: url("../../assets/rejected.png");
}
